import { Routes, Route, Navigate } from 'react-router-dom';
import { useEffect, lazy, Suspense } from 'react';
import { useAuthStore } from '@/stores/auth-store';
import { useSettingsStore } from '@/stores/settings-store';
import LoadingSpinner from '@/components/LoadingSpinner';
import { isDemoMode, demoUser, demoToken } from '@/lib/demo-data';

// è·¯ç”±çº§ä»£ç åˆ†å‰²ï¼šæ‡’åŠ è½½é¡µé¢ç»„ä»¶
const LoginPage = lazy(() => import('@/pages/LoginPage'));
const RegisterPage = lazy(() => import('@/pages/RegisterPage'));
const NotesPage = lazy(() => import('@/pages/NotesPage'));
const AdminInviteCodesPage = lazy(() => import('@/pages/AdminInviteCodesPage'));

function App() {
  const isAuthenticated = useAuthStore((state) => state.isAuthenticated);
  const login = useAuthStore((state) => state.login);
  const theme = useSettingsStore((state) => state.theme);
  const setTheme = useSettingsStore((state) => state.setTheme);

  // ğŸ­ Demo æ¨¡å¼ï¼šè‡ªåŠ¨ç™»å½•
  useEffect(() => {
    if (isDemoMode() && !isAuthenticated) {
      console.log('ğŸ­ Demo æ¨¡å¼å·²å¯ç”¨ï¼Œè‡ªåŠ¨ç™»å½•...');
      login(demoUser, demoToken);
    }
  }, [isAuthenticated, login]);

  // åˆå§‹åŒ–ä¸»é¢˜
  useEffect(() => {
    // åº”ç”¨ä¸»é¢˜ï¼ˆè§¦å‘ setTheme ç¡®ä¿ DOM æ›´æ–°ï¼‰
    setTheme(theme);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 overflow-x-hidden max-w-full">
      <Suspense fallback={<LoadingSpinner />}>
        <Routes>
          <Route
            path="/login"
            element={!isAuthenticated ? <LoginPage /> : <Navigate to="/notes" replace />}
          />
          <Route
            path="/register"
            element={!isAuthenticated ? <RegisterPage /> : <Navigate to="/notes" replace />}
          />
          <Route
            path="/notes"
            element={isAuthenticated ? <NotesPage /> : <Navigate to="/login" replace />}
          />
          <Route
            path="/admin"
            element={isAuthenticated ? <AdminInviteCodesPage /> : <Navigate to="/login" replace />}
          />
          <Route
            path="/"
            element={<Navigate to={isAuthenticated ? "/notes" : "/login"} replace />}
          />
        </Routes>
      </Suspense>
    </div>
  );
}

export default App;
