import { create } from 'zustand';
import { persist } from 'zustand/middleware';

export type Theme = 'light' | 'dark';
export type Language = 'zh-CN' | 'en-US';
export type ItemsPerPage = 25 | 50 | 100;

interface SettingsState {
  theme: Theme;
  language: Language;
  itemsPerPage: ItemsPerPage; // åˆ†é¡µå¤§å°

  setTheme: (theme: Theme) => void;
  setLanguage: (language: Language) => void;
  setItemsPerPage: (itemsPerPage: ItemsPerPage) => void;

  // è·å–å®é™…åº”ç”¨çš„ä¸»é¢˜
  getResolvedTheme: () => 'light' | 'dark';
  // è·å–å®é™…åº”ç”¨çš„è¯­è¨€ï¼ˆè€ƒè™‘æµè§ˆå™¨è¯­è¨€ï¼‰
  getResolvedLanguage: () => Language;
}

// æ£€æµ‹æµè§ˆå™¨ä¸»é¢˜åå¥½
const getSystemTheme = (): 'light' | 'dark' => {
  if (typeof window === 'undefined') return 'dark';
  return window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
};

// æ›´æ–°ä¸»é¢˜è‰² meta æ ‡ç­¾ï¼ˆPWA æ”¯æŒï¼‰
const updateThemeColorMeta = (theme: 'light' | 'dark') => {
  if (typeof window === 'undefined') return;

  const themeColor = theme === 'dark' ? '#1e293b' : '#3b82f6';
  let metaTag = document.querySelector('meta[name="theme-color"]');

  if (!metaTag) {
    metaTag = document.createElement('meta');
    metaTag.setAttribute('name', 'theme-color');
    document.head.appendChild(metaTag);
  }

  metaTag.setAttribute('content', themeColor);
};

// æ£€æµ‹æµè§ˆå™¨è¯­è¨€åå¥½
const getSystemLanguage = (): Language => {
  if (typeof window === 'undefined') return 'zh-CN';
  const lang = navigator.language || 'zh-CN';
  return lang.startsWith('zh') ? 'zh-CN' : 'en-US';
};

export const useSettingsStore = create<SettingsState>()(
  persist(
    (set, get) => ({
      // åˆå§‹ä¸»é¢˜æ ¹æ®ç³»ç»Ÿåå¥½è‡ªåŠ¨è®¾ç½®
      theme: getSystemTheme(),
      // åˆå§‹è¯­è¨€æ ¹æ®æµè§ˆå™¨è¯­è¨€è‡ªåŠ¨è®¾ç½®
      language: getSystemLanguage(),
      // é»˜è®¤åˆ†é¡µå¤§å°ï¼š25 æ¡/é¡µ
      itemsPerPage: 25,

      setTheme: (theme) => {
        set({ theme });

        // åº”ç”¨ä¸»é¢˜åˆ° DOM
        if (theme === 'dark') {
          document.documentElement.classList.add('dark');
        } else {
          document.documentElement.classList.remove('dark');
        }

        // æ›´æ–° PWA ä¸»é¢˜è‰²
        updateThemeColorMeta(theme);
      },

      setLanguage: (language) => {
        set({ language });
      },

      setItemsPerPage: (itemsPerPage) => {
        set({ itemsPerPage });
      },

      getResolvedTheme: () => {
        const { theme } = get();
        return theme;
      },

      getResolvedLanguage: () => {
        const { language } = get();
        return language;
      },
    }),
    {
      name: 'anynote-settings-storage',
      onRehydrateStorage: () => (state) => {
        // æ¢å¤çŠ¶æ€åç«‹å³åº”ç”¨ä¸»é¢˜
        if (state) {
          if (state.theme === 'dark') {
            document.documentElement.classList.add('dark');
          } else {
            document.documentElement.classList.remove('dark');
          }
          // æ›´æ–° PWA ä¸»é¢˜è‰²
          updateThemeColorMeta(state.theme);
        }
      },
    }
  )
);
